import { useState, useEffect } from 'react';
import { getClients, addClient } from '../src/app/services/api';

interface Client {
  id: number;
  name: string;
  joinedQueue: string;
}

const Clients = () => {
  const [clients, setClients] = useState<Client[]>([]);
  const [newClient, setNewClient] = useState({ name: '', joinedQueue: '' });

  useEffect(() => {
    async function fetchClients() {
      const response = await getClients();
      setClients(response.data);
    }
    fetchClients();
  }, []);

  const handleAddClient = async () => {
    const response = await addClient(newClient);
    setClients([...clients, response.data]);
    setNewClient({ name: '', joinedQueue: '' });
  };

  return (
    <div>
      <h1>Clientes</h1>
      <ul>
        {clients.map(client => (
          <li key={client.id}>
            {client.name} - {client.joinedQueue}
          </li>
        ))}
      </ul>
      <div>
        <h2>Agregar Cliente</h2>
        <input
          type="text"
          value={newClient.name}
          onChange={(e) => setNewClient({ ...newClient, name: e.target.value })}
          placeholder="Nombre"
        />
        <input
          type="datetime-local"
          value={newClient.joinedQueue}
          onChange={(e) => setNewClient({ ...newClient, joinedQueue: e.target.value })}
        />
        <button onClick={handleAddClient}>Agregar</button>
      </div>
    </div>
  );
};

export default Clients;